.skillsBlock {
    color: purple;
    min-height: 100vh;
}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    background-color: olive;

}

.skills {
    min-width: 100%;
    border: 1px solid cornflowerblue;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 5px;
    background-color: mediumpurple;
}
